import ChevronDownIcon from 'vue-material-design-icons/ChevronDown.vue';
export default {
  name: 'EditProfileAnchorLink',
  components: {
    ChevronDownIcon
  },
  props: {
    profileEnabled: {
      type: Boolean,
      required: true
    }
  },
  computed: {
    disabled() {
      return !this.profileEnabled;
    }
  }
};