import { loadState } from '@nextcloud/initial-state';
import Locale from './Locale.vue';
import HeaderBar from '../shared/HeaderBar.vue';
import { ACCOUNT_SETTING_PROPERTY_ENUM, ACCOUNT_SETTING_PROPERTY_READABLE_ENUM } from '../../../constants/AccountPropertyConstants.js';
const {
  localeMap: {
    activeLocale,
    localesForLanguage,
    otherLocales
  }
} = loadState('settings', 'personalInfoParameters', {});
export default {
  name: 'LocaleSection',
  components: {
    Locale,
    HeaderBar
  },
  data() {
    return {
      propertyReadable: ACCOUNT_SETTING_PROPERTY_READABLE_ENUM.LOCALE,
      localesForLanguage,
      otherLocales,
      locale: activeLocale
    };
  },
  computed: {
    inputId() {
      return "account-setting-".concat(ACCOUNT_SETTING_PROPERTY_ENUM.LOCALE);
    },
    isEditable() {
      return Boolean(this.locale);
    }
  }
};