import NcButton from '@nextcloud/vue/dist/Components/NcButton.js';
import Plus from 'vue-material-design-icons/Plus.vue';
import FederationControl from './FederationControl.vue';
import { ACCOUNT_PROPERTY_READABLE_ENUM, PROFILE_READABLE_ENUM } from '../../../constants/AccountPropertyConstants.js';
export default {
  name: 'HeaderBar',
  components: {
    FederationControl,
    NcButton,
    Plus
  },
  props: {
    scope: {
      type: String,
      default: null
    },
    readable: {
      type: String,
      required: true
    },
    inputId: {
      type: String,
      default: null
    },
    isEditable: {
      type: Boolean,
      default: true
    },
    isMultiValueSupported: {
      type: Boolean,
      default: false
    },
    isValidSection: {
      type: Boolean,
      default: true
    },
    isHeading: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      localScope: this.scope
    };
  },
  computed: {
    isProfileProperty() {
      return this.readable === ACCOUNT_PROPERTY_READABLE_ENUM.PROFILE_ENABLED;
    },
    isSettingProperty() {
      return !Object.values(ACCOUNT_PROPERTY_READABLE_ENUM).includes(this.readable) && !Object.values(PROFILE_READABLE_ENUM).includes(this.readable);
    }
  },
  methods: {
    onAddAdditional() {
      this.$emit('add-additional');
    },
    onScopeChange(scope) {
      this.$emit('update:scope', scope);
    }
  }
};