import { loadState } from '@nextcloud/initial-state';
import NcProgressBar from '@nextcloud/vue/dist/Components/NcProgressBar.js';
import Account from 'vue-material-design-icons/Account.vue';
import CircleSlice from 'vue-material-design-icons/CircleSlice3.vue';
import HeaderBar from './shared/HeaderBar.vue';

/** SYNC to be kept in sync with `lib/public/Files/FileInfo.php` */
const SPACE_UNLIMITED = -3;
const {
  groups,
  quota,
  totalSpace,
  usage,
  usageRelative
} = loadState('settings', 'personalInfoParameters', {});
export default {
  name: 'DetailsSection',
  components: {
    Account,
    CircleSlice,
    HeaderBar,
    NcProgressBar
  },
  data() {
    return {
      groups,
      usageRelative
    };
  },
  computed: {
    quotaText() {
      if (quota === SPACE_UNLIMITED) {
        return t('settings', 'You are using <strong>{usage}</strong>', {
          usage
        });
      }
      return t('settings', 'You are using <strong>{usage}</strong> of <strong>{totalSpace}</strong> (<strong>{usageRelative}%</strong>)', {
        usage,
        totalSpace,
        usageRelative
      });
    }
  }
};